.section
 {
   padding: 0px calc(50% - 480px);
 }

.card__heading {
  font-family: CoreSansG-Bold, sans-serif;
  width: 404px;
  display: block;
  text-align: left;
  font-size: 18px;
  margin-bottom: 16px;
 }

 .card__form {
   margin-top: 28px;
   border: 1px solid #E4E4E4;
   border-radius: 4px;

   display: flex;
   flex-direction: column;
   padding: 40px 100px;
   align-items: center;
 }

.card__input {
  font-size: 16px;
  font-family: CoreSansG-Regular, sans-serif;
  box-sizing: border-box;
  padding-left: 16px;
  // margin-top: 16px;
  display: block;
  border: 1px solid #CCCCCC;
  border-radius: 4px;
  width: 404px;
  height: 56px;
  outline: none;
  transition: border, box-shadow 0.3s ease;
  // background-image: url('../../images/master.png');
  background-repeat: no-repeat;
  background-position: 90% 50%;
}

.card__input:focus {
  -webkit-box-shadow: 3px 7px 23px 0px rgba(46,74,117,0.2);
  -moz-box-shadow: 3px 7px 23px 0px rgba(46,74,117,0.2);
  box-shadow: 3px 7px 23px 0px rgba(46,74,117,0.2);
}

.card__input:focus {
  border: 1px solid #93D0D9;
}

.card__input--small{
  width: 194px;
  background-image: none;
}

.card__input--error {
  border-color: #FF8D8D;
}

.card__wrapper {
  width: 404px;
  display: flex;
  justify-content:space-between;
}

.card__info {
  display: block;
  margin-top: 19px;
  width: 394px;
  text-align: left;
  font-family: CoreSansG-Regular, sans-serif;
  font-size: 16px;
  color: #808080;
  line-height: 20px;
}

.card__button {
  margin-top: 32px;
  box-sizing: border-box;
  width: 404px;
  height: 56px;
  background: linear-gradient(122.5deg, #5A4BE6 -33.07%, #73AFF7 48.35%, #93D0D9 139.94%);
  border-radius: 4px;
  outline: none;
  font-family: CoreSansG-Regular, sans-serif;
  font-size: 16px;
  line-height: 17px;
  color: #FFF;
  border-radius: 4px;
  border: none;
  transition: box-shadow 0.5s ease
}

.card__button:hover {
  cursor: pointer;
  -webkit-box-shadow: 3px 7px 23px 0px rgba(115,175,247,1);
  -moz-box-shadow: 3px 7px 23px 0px rgba(115,175,247,1);
  box-shadow: 3px 7px 23px 0px rgba(115,175,247,1);
}

.card__button:active {
  -webkit-box-shadow: 3px 7px 23px 0px rgba(84,158,242,1);
-moz-box-shadow: 3px 7px 23px 0px rgba(84,158,242,1);
box-shadow: 3px 7px 23px 0px rgb(55, 78, 104);
}

.card__error {
  width: 404px;
  height: 16px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #FF8D8D;
  text-align: left;
}

.card__error--small {
  width: 200px;
}

.errors-wrapper {
  display: flex;
  justify-content: space-between;
}

// .card__input::placeholder {
//   margin-left: 10px;
// }

@media screen and (max-width: 600px) {
  .card__button {
    width: 295px;
  }

  .card__input {
    width: 295px;
  }
  .card__wrapper {
    width: 295px;
  }

  .card__input--small{
    width: 144px;
  }

  .card__error {
    width: 295px;
  }

  .errors-wrapper {
    width: 295px;
  }

  .card__info {
    width: 287px;
  }

  .card__heading {
    width: 295px;
  }

  .section {
    padding: 0px 10px;
  }

}
