
.header {
  width: 100%;
  height: 100px;
  background: linear-gradient(90deg, #5A4BE6 0.26%, #73AFF7 47.1%, #93D0D9 99.8%);

  text-align: center;
  line-height: 100px;

  text-transform: uppercase;
  font-family: 'CoreSansG-Regular';
  // font-weight: bold;
  font-size: 32px;
  letter-spacing: 2px;
  color: #CFE0F4;

  position: relative;
}

.header__logo {
  height: 26px;
}

.language-toggler {
  height: 19px;
  position: absolute;
  top: 0;
  right: 5%;
}

.language-toggler__link {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  letter-spacing: -0.2px;
  background-color: transparent;
  border: none;
  outline: none;
  color: #fff;
}

.language-toggler__link--active {
  color: #587BC3;
}

.language-toggler__link:first-child::after {
  content: " | ";
  color: #fff;
}

@media screen and (max-width: 600px) {
  .header {
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
    padding: 0px 25px;
  }

  .header__logo {
    height: 16px;
  }

  .heading {
    padding: 15px;
  }

  .language-toggler {
    right: 15%;
  }
}
